<script>
	import _ from "lodash";
	import Slider from '@smui/slider';
	import IconButton from "@smui/icon-button";
	import { Row, Cell } from "@smui/data-table";
	import Textfield from "@smui/textfield";
	import FormField from "@smui/form-field";
	import { createEventDispatcher } from 'svelte';

	export let percentage;
	export let name;
	export let description;
	export let id;

	const dispatch = createEventDispatcher();

	let disabled = false;

	async function save() {
		let update = { "Id": id, "Description": description, "Enabled": checked, "Type": "PercentageFlag", "Percent": percentage, "Strings": "" };
		const options = {
			method: "POST",
			body: JSON.stringify(update),
			headers: {	"Content-Type": "application/json",},
		};

		disabled = true;
		let res = await fetch("/veff_internal_api/update", options);
		if (res.ok) {
			dispatch("saved", update)
		} else {
			dispatch("error", {message: "something went bad" })
		}
		
		disabled = false;
	}

</script>

<Row>
	<Cell><b>{name}</b></Cell>
	<Cell style="padding:1rem;">
		<FormField>
			<Slider style="flex-grow: 1;" 
			bind:percentage
			min={0}
			max={100}
			step={1}
			discrete
			/>
			<span slot="label">% enabled</span>
		</FormField>
	</Cell>
	<Cell>
		<Textfield
			input$resizable={false}
			style="width: 100%;"
			bind:value={description}
			label="description"
		/>
	</Cell>
	<Cell>
		<IconButton class="material-icons" {disabled} on:click={() => save()}>save</IconButton>
	</Cell>
</Row>

